<template>
	<div class="container">
        <div class="blog-article" v-for='article in articles' :key='article.index'>
           <div v-if='article.id === proId'>
                <FormPost :article='article' />
            </div>
        </div>
        <!-- /.menu-item -->
	</div>
</template>
<script>
import { db } from '@/main';
import FormPost from './FormPost';
import cyrillicToTranslit from 'cyrillic-to-translit-js';

export default {
    data() {
        return {
            proId: this.$route.params.id,
            articles: [],
        }
    },
    methods: {
        friendlyUrl(value) {
            return cyrillicToTranslit({ preset: "uk" }).transform(value.toLowerCase(), "_")
        },
    },
    firestore () {
        return {
            articles: db.collection('blog').orderBy('createdAt')
        }
    },
    components: {
        FormPost
    }
}
</script>
